import { Component, OnInit } from '@angular/core';
import { MarkSixResult } from '../models/common.model';
import { CommonService } from '../services/common.service';
import { Mark6Service } from '../services/mark6.service';
import { Router } from '@angular/router';
import { NavController, LoadingController } from '@ionic/angular';

@Component({
  selector: 'app-last30',
  templateUrl: './last30.component.html',
  styleUrls: ['./last30.component.scss']
})
export class Last30Component implements OnInit {

  public loaded = false;
  public m6results: MarkSixResult[];

  constructor(private commonService: CommonService, private m6service: Mark6Service, private _router: Router,
    private navCtrl: NavController, private loadingController: LoadingController) { }

  ngOnInit() {
    this.doRefresh();
  }

  async doRefresh() {
    this.loaded = false;

    let loading = await this.loadingController.create({
      message: 'Loading',
      spinner: 'circles'
    });

    loading.present().then(() => {
      this.m6service.getLast30().subscribe(result => {
        this.m6results = result;
        this.loaded = true;
        this.m6service.result = result;
        loading.dismiss();
      }, error => {
        loading.dismiss();
        this.loaded = false;
        console.log(error);
      });
    });
  }

  async showDetail(val) {
    // console.log(val);
    // this._router.navigate(['last30/detail' , val]);

    let loading = await this.loadingController.create({
      message: 'Loading',
      spinner: 'circles'
    });

    loading.present().then(() => {
      this.navCtrl.navigateForward(['last30/detail' , val]);
      loading.dismiss();
    });
  }

  toDate(val): string {
    if (val != null) {
      return this.m6service.toDateStr(val);
    } else {
      return '';
    }
  }

}
